@import url(https://fonts.googleapis.com/css?family=Nunito:300,400,600,700,800,900&display=swap);
body * {
  font-family: "Nunito", sans-serif; }

header {
  position: fixed;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 2; }
  @media (max-width: 768px) {
    header nav.navbar {
      justify-content: space-between; } }
  header span.navbar-toggler-icon {
    position: relative; }
    header span.navbar-toggler-icon:before {
      content: "";
      width: 100%;
      height: 4px;
      background-color: #000;
      position: absolute;
      left: 0;
      top: 35%;
      border-radius: 50px;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%); }
    header span.navbar-toggler-icon:after {
      content: "";
      width: 84%;
      height: 4px;
      background-color: #000;
      position: absolute;
      left: 0;
      top: 60%;
      border-radius: 50px;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%); }
  header .navbar-brand h1 {
    margin: 0;
    font-size: 26px;
    color: #000000;
    font-weight: 800; }
  header ul.navbar-nav {
    width: 100%;
    justify-content: flex-start; }
    header ul.navbar-nav li a {
      -webkit-transition: all 0.3s linear;
      transition: all 0.3s linear;
      padding: 0 20px;
      display: block;
      text-decoration: none;
      color: #965050;
      font-weight: 700; }
      header ul.navbar-nav li a:hover {
        color: #e03c3c; }
  header .badge.badge-cart {
    padding: 0 15px;
    height: 40px;
    line-height: 40px;
    border: 2px solid #000;
    border-radius: 50px;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
    width: 65px;
    display: flex;
    cursor: pointer;
    overflow: hidden; }
    header .badge.badge-cart span.icon {
      height: 100%;
      line-height: 46px; }
      header .badge.badge-cart span.icon svg {
        font-size: 24px;
        color: #000000;
        -webkit-transition: all 0.3s linear;
        transition: all 0.3s linear; }
    header .badge.badge-cart .sub-total {
      width: auto;
      overflow: hidden;
      -webkit-transition: all 0.4s linear;
      transition: all 0.4s linear;
      opacity: 0;
      height: 100%;
      line-height: 38px; }
      header .badge.badge-cart .sub-total span {
        color: #000;
        margin-left: 10px;
        font-size: 14px;
        color: #000; }
    @media (min-width: 376px) {
      header .badge.badge-cart:hover {
        border-color: #000;
        width: 190px;
        max-width: 100%;
        min-width: 80px;
        width: 155px; }
        header .badge.badge-cart:hover .icon svg {
          color: #000; }
        header .badge.badge-cart:hover .sub-total {
          width: auto;
          opacity: 1; } }

.showcase {
  padding-top: 1.5rem; }
  .showcase h2 {
    margin: 0 0 1.5rem 0;
    text-align: center;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 20px; }
  .showcase ul {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: row; }
    .showcase ul li {
      text-align: center;
      padding-bottom: 25px; }
      .showcase ul li .product {
        background-color: #fff;
        border-radius: 8px;
        box-shadow: 2px 3px 5px 0px rgba(0, 0, 0, 0.3);
        border: 2px solid #f5f5f5; }
        .showcase ul li .product a {
          display: block;
          cursor: pointer;
          padding: 20px; }
        .showcase ul li .product .product-image {
          height: 180px;
          padding: 20px;
          line-height: 180px;
          box-sizing: content-box; }
          .showcase ul li .product .product-image img {
            max-height: 100%;
            max-width: 100%;
            width: auto; }
        .showcase ul li .product .product-content h3.title {
          font-weight: bold;
          font-size: 12px;
          margin: 0 0 1.5rem 0;
          height: 30px; }
        .showcase ul li .product .product-content .price-container {
          text-align: center; }
          .showcase ul li .product .product-content .price-container span {
            color: #000;
            font-size: 16px;
            font-weight: 700; }
        .showcase ul li .product:hover {
          box-shadow: 2px 3px 5px 0px rgba(0, 0, 0, 0.5);
          border-color: transparent; }

.fullbanner {
  width: 100%;
  height: 400px;
  overflow: hidden;
  position: relative;
  background-image: url("https://images.pexels.com/photos/932577/pexels-photo-932577.jpeg?auto=compress&cs=tinysrgb&h=750&w=1260");
  background-position: 0 -160px;
  background-size: cover;
  background-repeat: no-repeat; }
  .fullbanner:before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(25, 25, 25, 0.4);
    z-index: 1;
    display: block;
    position: absolute; }
  @media (max-width: 767px) {
    .fullbanner {
      height: 200px;
      background-position: center; } }

@media (max-width: 767px) {
  div#navbarSupportedContent {
    display: block;
    position: fixed;
    top: 0;
    left: -100%;
    width: 0;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    padding: 0;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear; }
    div#navbarSupportedContent button {
      position: absolute;
      right: 45px;
      width: 45px;
      top: 35px;
      border: none;
      height: 45px;
      padding: 0;
      box-sizing: border-box;
      border-radius: 8px;
      background-color: #ff9595; }
    div#navbarSupportedContent ul {
      height: 100%;
      width: 80%;
      padding: 20px;
      background-color: #000; }
      div#navbarSupportedContent ul li a {
        padding: 5px 0px; }
    div#navbarSupportedContent.active {
      width: 100%;
      left: 0; } }

